@import url("https://fonts.googleapis.com/css?family=Atkinson+Hyperlegible:400&display=swap");

.contact-container {
 max-width: 800px;
 margin: 0 auto;
 padding: 20px;
}

.contact-container h1 {
 font-family: 'Atkinson Hyperlegible', sans-serif;
 font-weight: 400;
 font-style: italic;
 letter-spacing: 2px;
 text-align: center;
 margin-bottom: 20px;
}

.contact-container > p {
 text-align: center;
 font-size: 1.1em;
 line-height: 1.6;
 color: #555;
 margin-bottom: 20px;
}

.email-highlight {
 text-align: center;
 margin: 30px 0;
 padding: 30px;
 background-color: #f9f9f9;
 border-radius: 5px;
}

.email-link {
 font-size: 1.4em;
 color: #007bff;
 text-decoration: none;
 font-weight: 500;
}

.email-link:hover {
 text-decoration: underline;
 color: #0056b3;
}

/* Contact Methods (Buttons) */
.contact-methods {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 12px;
 max-width: 500px;
 margin: 40px auto;
}

@media (max-width: 768px) {
 .contact-methods {
 grid-template-columns: 1fr;
 }
}

.contact-button {
 display: inline-flex;
 align-items: center;
 gap: 8px;
 padding: 12px 20px;
 border-radius: 6px;
 text-decoration: none;
 font-size: 0.95em;
 font-weight: 500;
 transition: all 0.3s ease;
 border: 2px solid;
 justify-content: center;
}

.contact-button svg {
 flex-shrink: 0;
}

.contact-button.email {
 background-color: #007bff;
 color: white;
 border-color: #007bff;
}

.contact-button.email:hover {
 background-color: #0056b3;
 border-color: #004085;
 transform: translateY(-2px);
 box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

.contact-button.linkedin {
 background-color: #0077b5;
 color: white;
 border-color: #0077b5;
}

.contact-button.linkedin:hover {
 background-color: #005582;
 border-color: #004471;
 transform: translateY(-2px);
 box-shadow: 0 4px 12px rgba(0, 119, 181, 0.3);
}

.contact-button.github {
 background-color: #24292e;
 color: white;
 border-color: #24292e;
}

.contact-button.github:hover {
 background-color: #1b1f23;
 border-color: #14171a;
 transform: translateY(-2px);
 box-shadow: 0 4px 12px rgba(36, 41, 46, 0.3);
}

.form-section {
 margin: 40px 0;
 padding: 30px 20px;
 background-color: #f9f9f9;
 border-radius: 5px;
}

.success-message {
 background-color: #4CAF50;
 color: white;
 padding: 10px;
 border-radius: 5px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 max-width: 400px;
 margin: 20px auto 0;
}

.close-button {
 cursor: pointer;
}

form {
 max-width: 400px;
 margin: 0 auto;
 margin-bottom: 20px;
}

label {
 display: block;
 margin-bottom: 5px;
 font-weight: 500;
}

input[type="text"],
input[type="email"],
textarea {
 width: 100%;
 padding: 10px;
 margin-bottom: 15px;
 border: 1px solid #ddd;
 border-radius: 3px;
 font-family: inherit;
 font-size: 1em;
}

input[type="submit"] {
 background-color: #007bff;
 color: white;
 padding: 12px 30px;
 border: none;
 border-radius: 3px;
 cursor: pointer;
 font-size: 1em;
 transition: background-color 0.3s ease;
}

input[type="submit"]:hover {
 background-color: #0056b3;
}

@media (max-width: 600px) {
 .contact-container {
 padding: 10px;
 }
 
 .email-highlight {
 padding: 20px;
 }
 
 .email-link {
 font-size: 1.2em;
 }
 
 .form-section {
 padding: 20px 10px;
 }
 
 form {
 max-width: 100%;
 }
}